/*------ Footer ---------*/

.aside-footer {
	padding: 1rem 1.5rem;
	border-block-start: 1px solid $border;
}

.footer {
	background: $white;
	border-block-start: 1px solid $border;
	font-size: 0.875rem;
	padding: 1.25rem 1.25rem 1.25rem 230px;
	.container {
		padding-inline-start: 0.75rem;
		padding-inline-end: 0.75rem;
	}
	.social {
		ul li {
			float: $float-start;
			padding: 7px;
			a {
				color: #7a7484;
			}
		}
		margin: 0 auto;
		width: auto;
	}
}

@media print {
	.footer {
		display: none;
	}
}

.footer-1.footer {
	padding: 1.25rem 1.25rem 1.25rem 1.25rem;
}

.top-footer {
	width: 100%;
	line-height: 1.25rem;
	opacity: 1;
	transform: translate(0px, 0px);
	font-size: 14px;
	flex: 0 0 auto;
	h6 {
		font-size: 14px;
		margin-block-end: 1rem;
		font-weight: 500;
	}
	p {
		color: $muted;
	}
	a {
		color: $muted;
		line-height: 2;
		address {
			color: $muted;
			margin-block-end: 0;
			line-height: 2;
		}
	}
	img {
		width: 80px;
		border: 1px solid #e6eaea;
		padding: 2px;
		height: 80px;
		margin: 2px;
		&:hover {
			color: #8e9090;
			opacity: .6;
		}
	}
	.list-unstyled li {
		border-block-end: 0;
	}
}

.footer-payments {
	padding-inline-start: 0;
	list-style: none;
	margin: 0;
	line-height: 1.25rem;
	li {
		&:not(:last-child) {
			margin-inline-end: 1.5rem;
		}
		display: inline-block;
	}
	a {
		color: #a7a8c7;
		text-decoration: none;
		font-size: 2rem;
		&:hover {
			opacity: .9;
		}
	}
}

.main-footer {
	padding: 20px 30px 20px 280px;
	font-size: 14px;
	font-weight: 400;
	background-color: $white;
	border-block-start: 1px solid #e6eaea;
}

.footer-social-list {
	li {
		&:not(:last-child) {
			margin-inline-end: 1.5rem;
		}
		display: inline-block;
	}
	a {
		text-decoration: none;
		font-size: 1rem;
	}
	padding-inline-start: 0;
	list-style: none;
	margin: 0;
	line-height: 1.25rem;
}

@media (max-width: 992px) {
	.footer-social-list {
		text-align: center;
		margin-block-start: 1rem;
	}
	.footer1 {
		text-align: center;
	}
}

@media only screen and (max-width: 991.98px) {
	.footer {
		padding: 1.25rem 0 !important;
	}
}

.footer.hor-footer {
	padding: 1.25rem 1.25rem 1.25rem 0px;
}