    }
    .profile-img {
      inset-block-start: 0px;
      inset-inline-end: 19px;
    }
    .app-content {
      margin-inline-start: 80px;
    }
    .app-sidebar {
      inset-inline-start: 0;
      width: 80px;
      overflow: hidden;
      &:hover {
        overflow: visible;
      }
    }
    .slide-menu {
      position: absolute;
      opacity: 0;
      z-index: 9;
